@tailwind base;
@tailwind components;
@tailwind utilities;
/* ==============================================
.p-calendar
============================================== */
.p-calendar {
  position: relative;
}
.p-calendar__loading {
  position: absolute;
  z-index: 1000;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: calc(100% - 72px);
  background: rgba(0, 0, 0, 0.5);
  display: none;
}
.p-calendar__loading-icon {
  position: relative;
}
.p-calendar__loading::before {
  content: "";
  display: block;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 50px;
  height: 50px;
  border-radius: 5px;
  background: white;
}
.p-calendar__loading::after {
  content: "";
  display: block;
  position: fixed;
  left: 50%;
  top: 50%;
  translate: -50% -50%;
  width: 32px;
  height: 32px;
  border-radius: 20px;
  border: 4px solid #60ABB9;
  border-right: 4px solid white;
  animation: rotate 1s infinite linear;
}
@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.p-calendar__label {
  font-size: 32px;
  letter-spacing: 0.07em;
  font-weight: 700;
  padding-bottom: 24px;
}
.p-calendar__move {
  position: absolute;
  z-index: 999;
  top: 0;
  width: 50px;
  height: 50px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
}
.p-calendar__move::before, .p-calendar__move::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 13px;
  height: 3px;
  background-color: #333;
}
.p-calendar__move.js-prev {
  right: 60px;
}
.p-calendar__move.js-prev::before {
  transform-origin: top left;
  transform: translate(-50%, -50%) rotate(45deg);
}
.p-calendar__move.js-prev::after {
  transform-origin: bottom left;
  transform: translate(-50%, -50%) rotate(-45deg);
}
.p-calendar__move.js-next {
  right: 0;
}
.p-calendar__move.js-next::before {
  transform-origin: top right;
  transform: translate(-50%, -50%) rotate(-45deg);
}
.p-calendar__move.js-next::after {
  transform-origin: bottom right;
  transform: translate(-50%, -50%) rotate(45deg);
}
.p-calendar__body {
  position: relative;
  display: flex;
  gap: 30px;
}
.p-calendar__item {
  flex: 1;
}
.p-calendar__item:last-child {
  display: none;
}
.p-calendar__table {
  width: 100%;
  border-collapse: collapse;
}
.p-calendar__day, .p-calendar__date {
  border: 1px solid #333;
  width: 14.2857142857%;
}
.p-calendar__day {
  height: 42px;
  background-color: #777;
  text-align: center;
  color: #FFF;
  font-size: 20px;
  font-weight: 600;
}
.p-calendar__date .js-cell {
  width: 100%;
  min-height: 125px;
  padding: 6px 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.p-calendar__date .day {
  font-size: 20px;
  font-weight: 600;
  display: block;
  width: 100%;
  text-align: right;
  padding-right: 10px;
}
.p-calendar__events {
  position: relative;
  width: calc(100% - 10px);
  margin-left: 5px;
  margin-right: 5px;
  height: 60px;
  cursor: pointer;
  display: none;
  padding: 20px 5px 0;
}
.p-calendar__events:has(.p-calendar__events-item) {
  display: block;
}
.p-calendar__events-icons {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  transform: translateY(-50%);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 4px;
}
.p-calendar__events-icon {
  position: relative;
  max-width: 44px;
  flex: 1;
  background: linear-gradient(to bottom, rgba(119, 119, 119, 0.1882352941) 50%, transparent 50%);
  border-top-left-radius: 50%;
  border-top-right-radius: 50%;
  padding: 3px;
}
.p-calendar__events-icon span {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding-bottom: 100%;
  background-color: #fff;
  border-radius: 50%;
}
.p-calendar__events-icon img {
  position: absolute;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
  height: 50%;
  object-fit: contain;
}
.p-calendar__events-list {
  overflow: hidden;
  height: 36px;
  font-size: 0;
}
.p-calendar__events-item {
  color: #3C3C3C;
  text-align: center;
  font-size: 12px;
  font-weight: 700;
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  text-decoration: underline;
}

/* ==============================================
.p-calendar-modal
============================================== */
.p-calendar-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 9999999;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: opacity 0.3s;
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}
@media screen and (max-width: 767px) {
  .p-calendar-modal {
    position: static;
    background-color: transparent;
    height: auto;
  }
}
.p-calendar-modal.is-active {
  opacity: 1;
  pointer-events: auto;
  visibility: visible;
}
.p-calendar-modal__box {
  position: relative;
  max-width: 490px;
  width: 100%;
  background-color: #fff;
  padding: 64px 38px 50px;
}
@media screen and (max-width: 767px) {
  .p-calendar-modal__box {
    max-width: none;
    padding: 20px 0;
  }
}
.p-calendar-modal__close {
  position: absolute;
  width: 24px;
  height: 24px;
  top: 20px;
  right: 20px;
  cursor: pointer;
  background-size: contain;
  background-repeat: no-repeat;
  background-image: url(../img/icon_close.svg);
}
@media screen and (max-width: 767px) {
  .p-calendar-modal__close {
    display: none;
  }
}
.p-calendar-modal__item {
  position: relative;
  display: flex;
  align-items: center;
  gap: 10px;
}
@media screen and (max-width: 767px) {
  .p-calendar-modal__item {
    padding: 14px 10px;
  }
}
.p-calendar-modal__item + .p-calendar-modal__item {
  margin-top: 30px;
}
@media screen and (max-width: 767px) {
  .p-calendar-modal__item + .p-calendar-modal__item {
    margin-top: 10px;
  }
}
.p-calendar-modal__bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: none;
}
@media screen and (max-width: 767px) {
  .p-calendar-modal__bg {
    display: block;
  }
}
.p-calendar-modal__icon {
  position: relative;
  z-index: 1;
  width: 50px;
  height: 50px;
  border-width: 3px;
  border-style: solid;
  border-radius: 50%;
  margin-right: 10px;
}
.p-calendar-modal__icon .p-calendar__events-icon {
  display: contents;
}
.p-calendar-modal__icon img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
  height: 50%;
  object-fit: contain;
}
.p-calendar-modal__contents {
  position: relative;
  z-index: 1;
  width: calc(100% - 60px);
}
.p-calendar-modal__date {
  font-size: 12px;
  font-weight: 500;
  margin-bottom: 3px;
}
@media screen and (min-width: 768px) {
  .p-calendar-modal__date {
    color: #3C3C3C !important;
  }
}
.p-calendar-modal__title {
  font-size: 18px;
  font-weight: 700;
}
@media screen and (min-width: 768px) {
  .p-calendar-modal__title {
    color: #3C3C3C !important;
  }
}
.p-calendar-modal__text {
  font-size: 14px;
  margin-top: 10px;
}